<template>
    <gmap-map :center="options.center" :options="options" style="width: 100%; height: 450px">
        <gmap-custom-marker :marker="options.center" alignment="top" :offsetX="0" :offsetY="17.5" @click.native="gmapFunc">
            <img src="./img/map-marker.png" class="marker" />
        </gmap-custom-marker>
    </gmap-map>
</template>
<script>
    import Vue from 'vue'
    import * as VueGoogleMaps from 'vue2-google-maps';
    import GmapCustomMarker from 'vue2-gmap-custom-marker';

    Vue.use(VueGoogleMaps, {
        load: {
            key: 'AIzaSyD61B9tyc2LQvicG_-A4z9ap2NgH7WJY6s',
            v: '3.29'
        }
    });
    export default {
        data () {
            return {
                options: {
                    center: {
                        lat: 50.482553,
                        lng: 30.591479
                    },
                    zoom: 17,
                    styles: [{
                        stylers: [{
                            saturation: -100
                        }]
                    }]
                }
            }
        },
        components: {
            "gmap-custom-marker": GmapCustomMarker
        },
        methods: {
            gmapFunc() {
                let el = document.querySelector('.marker');
                if(!el.classList.contains("animate")) {
                    el.classList.add('animate');
                    setTimeout(function () {
                        el.classList.remove('animate');
                    }, 1000)
                }

            }
        }
    }
</script>