import jQuery from 'jQuery'
jQuery(document).ready(function () {
    jQuery(".anchor").click(function (e) {
        e.preventDefault();
        let elementClick = jQuery(this).attr("href");
        let destination = jQuery(elementClick).offset().top;
        jQuery("body,html").animate({scrollTop: destination }, 800);
    });
    // jQuery(".show-menu").click(function (e) {
    //     e.preventDefault();
    //     var el = jQuery(this).parents('.header').find('.header-menu');
    //     var currEl = jQuery(this);
    //     if(el.css('display') == 'none') {
    //         currEl.addClass('active');
    //         el.slideDown();
    //     }
    //     else {
    //         currEl.removeClass('active');
    //         el.slideUp();
    //     }
    // });
    jQuery('.fb-input, .feedback-area').focus(function() {
        jQuery(this).parent('div').addClass('active');
    });
    jQuery('.fb-input, .feedback-area').focusout(function() {
        if(jQuery(this).val() == "") {
            jQuery(this).parent('div').removeClass('active');
        }
    });
    jQuery('.wrapper').each(function(){
        var footHeight = jQuery(this).find('.footer').outerHeight();
        jQuery(this).css('padding-bottom', footHeight)
    });
    jQuery(window).scroll(function () {
        if (jQuery(this).scrollTop() > 450) {
            jQuery('#to-top').fadeIn();
        } else {
            jQuery('#to-top').fadeOut();
        }
    });
    jQuery('#to-top').click(function () {
        jQuery('body,html').animate({
            scrollTop: 0
        }, 800);
        return false;
    });
    if(jQuery(window).width() > 991) {
        let nav = jQuery('#sticky'),
            posTop = nav.position().top;
        jQuery(window).scroll(function () {
            var y = jQuery(this).scrollTop();
            if (y > posTop) { nav.addClass('fixed');}
            else { nav.removeClass('fixed');}
        });
    }
    if(jQuery(window).width() < 768) {
        var nav = jQuery('#show-sticky'),
            posTop = nav.position().top;
        jQuery(window).scroll(function () {
            var y = jQuery(this).scrollTop() - 15;
            if (y > posTop) { nav.addClass('fixed'); }
            else { nav.removeClass('fixed'); }
        });
    }
    jQuery('.form-check').submit(function(e){
        e.preventDefault();
        var _this = this;
        jQuery(_this).find('.send-form').prop('disabled', true);
        var form_data = jQuery(this).serialize();
        jQuery.ajax({
            type: "POST",
            url: "mail.php",
            data: form_data,
            success: function(data) {
                jQuery('.form-check')[0].reset();
                jQuery('.succes-modal').fadeIn();
                jQuery('.feedback-box').removeClass('active');
                jQuery('.fb-area').removeClass('active');
                setTimeout(function(){
                    jQuery('.succes-modal').fadeOut();
                    }, 2000);
            }
        }).always(function(){
            jQuery(_this).find('.send-form').prop('disabled', false);
        });
    });
});
jQuery(window).resize(function() {
    jQuery('.wrapper').each(function(){
        var footHeight = jQuery(this).find('.footer').outerHeight();
        jQuery(this).css('padding-bottom', footHeight)
    });
});
jQuery(window).on("load",function(){
    let preloader = jQuery('.areaForLoader');
    let loader = preloader.find('.loader');
    loader.fadeOut();
    preloader.delay(350).fadeOut('slow');
});
window.onload = function () {
    new Swiper('.swiper-container', {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        initialSlide: "1",
        coverflowEffect: {
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows : true,
        },
        pagination: {
            el: '.swiper-pagination',
        },
    });
};
